.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: -20%;
    opacity: 0.8;
    padding: 0.3rem 0.5rem;
    z-index: var(--z-tooltip);
}

.scrollup__icon {
    transition: var(--transition);
    font-size: 2rem;
}

.scrollup__icon:hover {
    color: var(--title-color-dark);
}

/* Show scrollup*/

.show-scroll {
    bottom: 3rem;
}

/* ================ BREAKPOINTS ================ */
/* For large devices */
@media screen and (max-width: 992px) {
    .scrollup {
        right: 1.5rem;
        padding: 0.25rem 0.4rem;
    }

    .scrollup__icon {
        font-size: 1.5rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .show-scroll {
        bottom: 4rem;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .scrollup {
        right: 1rem;
        padding: 0.25rem 0.4rem;
    }

}